import emailjs from 'emailjs-com'
import { useState } from "react"
import { validateParameters } from "./validateParameters"
import "./contactForm.css"

const ContactForm = () => {

    const [email, setEmail] = useState("")
    const [message, setMessage] = useState("")
    const [name, setName] = useState("")
    const [notification, setNotification] = useState(null)
    const [loading, setLoading] = useState(false)

    const resetParameters = () => {
        setEmail("")
        setMessage("")
        setName("")
    }

    const getClassName = () => {
        if (notification === null) {
            return ""
        }
        if (notification[0] === "error") {
            return "error"
        }
        if (notification[0] === "message") {
            return "message"
        }
    }

    const setNotificationTimeout = (type, content) => {
        setNotification([
            type,
            content
        ])
        setTimeout(() => {
            setNotification(null)
        }, 5000)
    }

    const handleSendForm = (event) => {
        event.preventDefault()
        const parameterError = validateParameters(name, email, message)
        if (parameterError) {
            setNotificationTimeout(parameterError.type, parameterError.message)
            return;
        }
        setLoading(true)
        //todo refactor out

        emailjs.sendForm("service_1ig5d1q", "template_m7tboxl", event.target, "user_EYozOZE6YPH3x5DgumnC8")
            .then(function (response) {
                console.log('SUCCESS!', response.status, response.text);
                resetParameters()
                setNotificationTimeout("message", "Message Sent Successfully!")
                setLoading(false)

            }, function (err) {
                console.log('FAILED...', err);
                setNotificationTimeout("error", err)
                setLoading(false)
            });
    }

    return (
        <div className="contact-form">
            <div className="notification">
                <h1 className={getClassName()}>{notification ? notification[1] : ""}</h1>
            </div>
            <h1>Got an idea for a commission, or a question about any of my products? Get in touch below!</h1>
            <form className="contact-form"
                onSubmit={handleSendForm}>
                <label>Name</label>
                <input type="text"
                    value={name}
                    disabled={loading}
                    name="from_name"
                    onChange={(event) => setName(event.target.value)}
                >
                </input>
                <label>Email</label>
                <input type="text"
                    value={email}
                    disabled={loading}
                    name="reply_to"
                    onChange={(event) => setEmail(event.target.value)}
                >
                </input>
                <label>Message</label>
                <textarea type="text"
                    value={message}
                    disabled={loading}
                    name="message"
                    rows="8"
                    cols="50"
                    onChange={(event) => setMessage(event.target.value)}
                >
                </textarea>
                <input type="submit" disabled={loading}></input>
            </form>
        </div>
    )
}

export { ContactForm }

